.raiting {
  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    margin-bottom: 32px;
    gap: 32px;

    @include big-tablet {
      grid-template-columns: repeat(9, 1fr);
    }

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
    }

    @include mobile {
      gap: 16px;
    }
  }

  &__item {
    grid-column: 3 span;

    &:nth-child(n + 9) {
      display: none;
    }

    @include big-tablet {
      &:nth-child(n + 7) {
        display: none;
      }
    }
  }

  &__btn {
    margin: 0 auto;
    padding: 20px 35px;
  }
}

.card {
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  border-radius: 10px;
  padding: 30px;
  min-height: 420px;
  background: $grey_light;

  &__title {
    margin: 0;
    margin-bottom: 23px;
    max-width: 236px;
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: $black;

    @include mobile {
      margin-bottom: 17px;
      font-size: 16px;
    }
  }

  &__price {
    margin-bottom: 23px;
    font-size: 16px;
    line-height: 130%;
    color: $grey_shade;

    @include mobile {
      margin-bottom: 15px;
    }
  }

  picture {
    display: flex;
    justify-content: center;
    margin-bottom: 22px;
    padding-top: 40px;
    transform: scale(1.12);

    @include mobile {
      margin-bottom: 17px;
      padding-top: 21px;
      transform: scale(1);
    }
  }

  &__raiting {
    position: absolute;
    left: 30px;
    top: 30px;
    padding-left: 23px;
    font-size: 16px;
    line-height: 130%;
    color: $secondary;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 2px;
      width: 16px;
      height: 15px;
      background-image: url("../../img/raiting-star.svg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;

      @include mobile {
        top: 0;
        width: 13px;
        height: 12px;
      }
    }

    @include mobile {
      left: 16px;
      top: 18px;
      padding-left: 19px;
      font-size: 12px;
    }
  }

  &__btn {
    @include mobile {
      padding: 10px 23px;
    }
  }

  @include big-mobile {
    padding: 16px;
    height: 100%;
    min-height: 360px;
  }

  @include mobile {
    padding: 16px;
    min-height: 278px;
  }
}

.raiting__item.is-visible {
  display: flex;
}
