.form {
  display: flex;
  flex-direction: column;

  &__label {
    position: relative;
    display: flex;
    margin-bottom: 30px;
  }

  &__input {
    border: 1px solid transparent;
    border-radius: 10px;
    padding: 6px 17px;
    width: 100%;
    font-size: 16px;
    line-height: 251%;
    background-color: $grey_light;
    transition: background-color 0.3s ease, border-color 0.3s ease;

    &::placeholder {
      color: $grey;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      background-color: $md_grey;
    }
  }

  &__btn {
    margin-bottom: 12px;
    padding: 17px 34px;
    font-weight: 600;
  }

  &__link {
    text-decoration: underline;
    color: $primary;

    &:focus {
      outline: none;
    }

    &:active {
      color: $primary_shade;
    }

    @include hover {
      color: $primary_light;
    }
  }
}

.custom-checkbox {
  @include custom-checkbox;
}

.focus-visible.form__input {
  border-color: $primary;
  background-color: $pink_light;
}

.focus-visible.form__link {
  background-color: $pink_light;
}

.just-validate-error-field {
  border-color: $red_input;
}

.just-validate-success-field {
  border-color: $green_input;
}

.just-validate-error-label {
  position: absolute;
  left: 18px;
  top: -18px;
  z-index: 5;
  font-size: 12px;
  line-height: 16px;
}

.checkbox-box {
  position: relative;

  .just-validate-error-label {
    left: 23px;
    top: auto;
    bottom: -18px;
  }
}
