.integration {
  &__content {
    @include bg-cover;

    padding-top: 150px;
    padding-bottom: 150px;
    background-image: url("../img/integration_bg.jpg");

    @include mobile {
      margin-bottom: 32px;
      padding-top: 30px;
      padding-bottom: 30px;
      min-height: 210px;
    }
  }

  &__container {
    @include small-tablet {
      padding: 0 36px;
    }

    @include mobile {
      padding: 0 12px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 49px;
    max-width: 40%;
    font-weight: 400;
    font-size: 48px;
    line-height: 57.6px;
    color: $white;

    @include big-tablet {
      max-width: 50%;
    }

    @include small-tablet {
      max-width: 70%;
    }

    @include big-mobile {
      max-width: 470px;
    }

    @include mobile {
      margin-bottom: 0;
      max-width: 200px;
      font-size: 16px;
      line-height: 120%;
    }
  }

  &__subtitle {
    display: inline-block;
    margin-bottom: -5px;
    font-family: "PlayfairDisplay", sans-serif;
    font-weight: 900;
    font-size: 60px;
    line-height: 135%;
    color: $gold;

    @include mobile {
      font-size: 24px;
    }
  }

  &__link {
    padding: 20px 30px;

    @include mobile {
      display: none;
    }

    &--two {
      display: none;

      @include mobile {
        display: block;
        margin: 0 auto;
        width: 296px;
      }
    }
  }
}
