.graph-modal {
  &__container {
    padding: 30px;
    width: 624px;
    min-height: 421px;

    @include big-mobile {
      padding: 30px 16px;
      width: 90%;
    }
  }

  &__close {
    right: 15px;
    top: 15px;
    width: 28px;
    height: 28px;
    background-image: none;

    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      border-radius: 3px;
      width: 100%;
      height: 3px;
      background-color: $grey;

      @include mobile {
        height: 2px;
      }
    }

    &::before {
      transform: rotate(-45deg);
    }

    &::after {
      transform: rotate(45deg);
    }

    @include mobile {
      right: 12px;
      top: 12px;
      width: 14px;
      height: 14px;
    }
  }
}

// Модальное окно - Форма
.modal-click {
  &__title {
    margin: 0;
    margin-bottom: 9px;
    font-weight: 400;
    font-size: 32px;
    line-height: 100%;
    color: $black;

    @include mobile {
      font-size: 24px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 30px;
    max-width: 374px;
    font-size: 14px;
    line-height: 19px;
    color: $black;

    @include mobile {
      margin-bottom: 16px;
    }
  }
}

.modal-click .form {
  &__label {
    @include mobile {
      margin-bottom: 16px;
    }
  }
}

// Модальное окно - Отправлено
.modal-send {
  &__svg {
    margin-bottom: 32px;
    width: 172px;
    height: 117px;
    fill: $secondary;

    @include mobile {
      margin-bottom: 14px;
      width: 73px;
      height: 50px;
    }
  }

  &__descr {
    margin: 0;
    font-size: 32px;
    line-height: 100%;
    color: $black;

    @include mobile {
      font-size: 16px;
    }
  }

  .graph-modal__content {
    position: absolute;
    left: 50%;
    top: 50%;
    width: max-content;
    text-align: center;
    transform: translate(-50%, -50%);

    @include mobile {
      width: auto;
    }
  }
}

.modal-send.graph-modal__container {
  @include mobile {
    min-height: 216px;
  }
}

// Модальное окно - Слайдер
.modal-swiper {
  &__picture {
    display: flex;
    justify-content: center;
    margin-bottom: 50px;

    @include big-mobile {
      margin-bottom: 32px;
    }
  }

  .swiper-wrapper {
    align-items: center;
  }
}

.modal-miniSwiper {
  padding: 20px 0;
  min-height: auto;

  &__picture {
    @include big-mobile {
      img {
        margin: 0 auto;
      }
    }
  }

  .swiper-slide {
    width: max-content;
  }

  @include tablet {
    margin: 0 59px;
  }

  @include big-mobile {
    margin: 0 46px;
    padding: 16px 0;
  }
}

.modal-slider.graph-modal__container {
  padding: 50px 50px 0;
  width: min(calc(var(--container-width) - var(--container-offset) * 2), 98%);

  .swiper-button-next,
  .swiper-button-prev {
    display: none;
  }

  .swiper {
    min-height: auto;

    @include big-mobile {
      margin-bottom: 0;
    }
  }

  @include big-tablet {
    padding: 50px 66px 0;

    .swiper-button-next,
    .swiper-button-prev {
      top: auto;
      bottom: 67px;
      display: flex;
    }

    .swiper-button-next {
      right: 16px;
    }

    .swiper-button-prev {
      left: 16px;
    }
  }

  @include tablet {
    width: min(calc(var(--container-width-tablet) - var(--container-offset-tablet) * 2), 93%);
  }

  @include small-tablet {
    padding: 50px 20px 0;
    width: min(calc(var(--container-width-small-tablet) - var(--container-offset-small-tablet) * 2), 93%);
  }

  @include big-mobile {
    padding: 32px 16px 0;
    width: calc(100% - var(--container-offset-big-mobile) * 2);
    min-height: auto;

    .swiper-button-next,
    .swiper-button-prev {
      bottom: 44px;
    }
  }

  @include mobile {
    width: calc(100% - var(--container-offset-mobile) * 2);
  }
}
