.noUi-horizontal {
  height: 3px;
}

.noUi-target {
  border: none;
  border-radius: 3px;
  box-shadow: none;
  background: $md_grey;
}

.noUi-connect {
  background-color: $primary;
}

.noUi-horizontal .noUi-handle {
  right: -6px;
  top: 0;
  -webkit-mask-image: url("../../img/triangle.svg");
  mask-image: url("../../img/triangle.svg");
  border: none;
  width: 13px;
  height: 13px;
  box-shadow: none;
  background-color: $primary;
}

.noUi-handle::after,
.noUi-handle::before {
  display: none;
}
