.useful {
  &__card {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    border-radius: 10px;
  }

  &__image {
    min-height: 426px;
    max-width: 624px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    &--bg1 {
      @include image-set2 ("../img/useful__bg1_x1", "../img/useful__bg1_x2");
    }

    &--bg2 {
      @include image-set2 ("../img/useful__bg2_x1", "../img/useful__bg2_x2");
    }

    &--bg3 {
      @include image-set2 ("../img/useful__bg3_x1", "../img/useful__bg3_x2");
    }

    &--bg4 {
      @include image-set2 ("../img/useful__bg4_x1", "../img/useful__bg4_x2");
    }

    &--bg5 {
      @include image-set2 ("../img/useful__bg5_x1", "../img/useful__bg5_x2");
    }

    @include big-tablet {
      min-height: 202px;
    }
  }

  &__content {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    padding: 30px;
  }

  &__descr {
    margin: 0;
    margin-bottom: 38px;
    font-weight: 400;
    font-size: 24px;
    line-height: 33px;
    color: $black;

    @include big-tablet {
      margin-bottom: 30px;
      font-size: 16px;
      line-height: 22px;
    }
  }

  &__btn {
    margin-top: auto;
  }

  .swiper-button-next,
  .swiper-button-prev {
    top: 3%;

    @include big-tablet {
      top: 4.5%;
    }

    @include mobile {
      top: auto;
      bottom: 0;
    }
  }

  &__swiper {
    @include mobile {
      padding-bottom: 48px;
    }
  }
}
