.product {
  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    margin-bottom: 82px;
    gap: 32px;

    @include big-tablet {
      grid-template-columns: repeat(9, 1fr);
      margin-bottom: 70px;
    }

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
      margin-bottom: 30px;
    }

    @include mobile {
      margin-bottom: 60px;
    }
  }

  &__images {
    grid-column: 6 span;

    @include big-tablet {
      grid-column: 5 span;
    }

    @include small-tablet {
      display: grid;
      grid-column: 6 span;
      grid-template-columns: repeat(6, 1fr);
      gap: 32px;
    }

    @include mobile {
      display: block;
    }
  }

  &__btn-slider {
    margin-bottom: 16px;

    @include small-tablet {
      grid-column: 6 span;
      margin-bottom: 0;
    }

    @include mobile {
      margin-bottom: 16px;
    }
  }

  &__group-images {
    display: flex;
    padding: 10px 25px;
    background-color: $grey_light;

    picture:not(:last-child) {
      display: block;
      margin-right: 38px;
    }

    @include big-tablet {
      overflow-x: auto;
      padding: 10px;

      picture {
        flex-shrink: 0;
      }
    }

    @include small-tablet {
      flex-direction: column;
      align-items: center;
      grid-column: 2 span;
      padding: 25px;

      picture:not(:last-child) {
        margin-right: 0;
        margin-bottom: 18.5px;
      }
    }

    @include mobile {
      flex-direction: row;
      padding: 10px;

      picture:not(:last-child) {
        margin-right: 38px;
        margin-bottom: 0;
      }
    }
  }

  &__content {
    grid-column: 6 span;
    padding-top: 14px;

    &--dubl {
      display: none;
    }

    @include big-tablet {
      grid-column: 4 span;
    }

    @include small-tablet {
      display: none;
      align-self: center;
      padding-top: 0;

      &--dubl {
        display: block;
        grid-column: 4 span;
      }
    }

    @include mobile {
      display: block;
      grid-column: 6 span;
      margin-top: -16px;

      &--dubl {
        display: none;
      }
    }
  }

  &__raiting {
    position: relative;
    margin-bottom: 3px;
    padding-left: 23px;
    font-size: 16px;
    line-height: 130%;
    color: $secondary;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 2px;
      width: 16px;
      height: 15px;
      background-image: url("../../img/raiting-star.svg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;

      // @include mobile {
      //   top: 0;
      //   width: 13px;
      //   height: 12px;
      // }
    }

    // @include mobile {
    //   left: 16px;
    //   top: 18px;
    //   padding-left: 19px;
    //   font-size: 12px;
    // }
  }

  &__title {
    margin: 0;
    margin-bottom: 22.5px;
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: $black;

    @include big-tablet {
      margin-bottom: 24.5px;
    }
  }

  &__names {
    margin-bottom: 5px;

    @include mobile {
      margin-bottom: 6px;
      font-size: 16px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 21px;
    font-size: 14px;
    line-height: 100%;
    color: $grey;

    @include mobile {
      margin-bottom: 17px;
      width: 80%;
    }
  }

  &__price {
    margin: 0;
    margin-bottom: 21px;
    font-size: 36px;
    line-height: 130%;
    color: $black;

    @include mobile {
      margin-bottom: 16px;
      font-size: 24px;
    }
  }

  &__btn {
    margin-bottom: 12px;
    padding: 17px 34px;
    font-weight: 600;

    @include mobile {
      width: 100%;
    }
  }

  &__btn--light {
    margin-left: 30px;
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: $primary;

    @include mobile {
      display: block;
      margin: 0 auto;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    grid-column: 6 span;

    @include big-tablet {
      grid-column: 5 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__caption {
    margin: 0;
    margin-bottom: 8px;
    font-weight: 600;
    font-size: 16px;
    line-height: 100%;
    color: $black;
  }

  &__desciption {
    margin: 0;
    font-size: 14px;
    line-height: 180%;
    color: $black;

    &:not(:last-of-type) {
      margin-bottom: 8px;
    }

    &:last-of-type {
      margin-bottom: 32px;
    }
  }

  &__back-images {
    display: flex;
    margin-top: auto;

    > picture:not(:last-child) {
      margin-right: 32px;

      @include mobile {
        margin-right: 16px;
      }
    }
  }

  &__list {
    grid-column: 6 span;

    @include big-tablet {
      grid-column: 4 span;
    }

    @include small-tablet {
      grid-column: 6 span;
    }
  }

  &__swiper {
    margin-bottom: 70px;

    .swiper-button-next,
    .swiper-button-prev {
      top: 4.6%;

      @include mobile {
        top: auto;
      }
    }

    @include mobile {
      padding-bottom: 48px;
    }
  }
}

.product-list {
  margin: 0;
  width: min(100%, 34rem);

  &__inner {
    display: flex;
    align-items: center;

    @include mobile {
      flex-direction: column;
      margin-bottom: 8px;
    }
  }

  &__key {
    position: relative;
    z-index: 0;
    margin-right: 0.25rem;
    width: min(100%, 34rem);
    font-size: 14px;
    line-height: 179%;
    color: $grey;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 16px;
      z-index: -1;
      width: 100%;
      height: 2px;
      background-image: url("../img/ellipses.svg");
      background-repeat: repeat-x;

      @include mobile {
        display: none;
      }
    }

    @include mobile {
      margin-right: 0;
      font-size: 10px;
      line-height: 14px;
    }
  }

  &__key-text {
    padding-right: 0.25rem;
    background-color: $white;

    @include mobile {
      padding-right: 0;
      background-color: transparent;
    }
  }

  &__value {
    flex-shrink: 0;
    margin: 0;
    width: min(100%, 13.5rem);
    font-size: 14px;
    line-height: 179%;
    color: $black;

    @include mobile {
      flex-shrink: 1;
      width: 100%;
      line-height: 19px;
    }
  }

  @include small-tablet {
    width: 100%;
  }
}
