.directory {
  &__general {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 32px;

    @include big-tablet {
      display: block;
    }
  }

  &__left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    grid-column: 3 span;

    @include big-tablet {
      display: none;
    }
  }

  &__top {
    display: none;

    @include big-tablet {
      display: grid;
      grid-template-columns: repeat(9, 1fr);
      margin-bottom: 32px;
      gap: 32px;
    }

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
      gap: 16px;
      column-gap: 32px;
    }

    @include mobile {
      gap: 16px;
    }
  }

  &__names {
    margin-bottom: 20px;
    font-size: 18px;
    line-height: 130%;
    color: $black;

    @include big-tablet {
      margin-bottom: 0;
      padding-top: 11px;
      font-size: 14px;
    }

    @include mobile {
      padding-top: 0;
      grid-column: 3 span;
    }
  }

  &__filter {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-bottom: 27px;

    &:nth-child(3) {
      margin-bottom: 36px;
    }

    @include big-tablet {
      grid-column: 2 span;
      margin-bottom: 0;

      &:nth-child(3) {
        margin-bottom: 0;
      }
    }

    @include small-tablet {
      &:nth-child(n + 2) {
        grid-row: 2/3;
      }

      &:nth-child(n + 4) {
        grid-row: 3/4;
      }
    }

    @include mobile {
      grid-column: 3 span;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 9px;
    font-weight: 600;
    font-size: 16px;
    line-height: 130%;
    color: $grey_shade;
  }

  &__checkbox {
    &:not(:last-child) {
      margin-bottom: 11px;
    }
  }

  &__checkbox-content {
    padding-left: 32px;
    font-weight: 400;
    font-size: 16px;
    line-height: 132%;
    color: $grey_shade;

    &::before {
      top: 1px;
      width: 20px;
      height: 20px;
    }

    &::after {
      left: 7px;
      top: 3px;
      width: 8px;
      height: 13px;
    }
  }

  &__select {
    @include big-tablet {
      position: relative;
      z-index: 15;
      border: 2px solid $white;
      border-radius: 10px;
      padding: 7px 13px;
      font-size: 16px;
      line-height: 22px;
      text-align: left;
      color: $black;
      background: $white;
      transition: border 0.3s ease;

      &::after {
        content: "";
        position: absolute;
        right: 16px;
        top: 40%;
        z-index: 3;
        margin-top: -2.5px;
        border: 1px solid $primary;
        border-width: 0 0 1px 1px;
        width: 8px;
        height: 8px;
        transform: rotate(-45deg);
        transition: border-color 0.3s ease, transform 0.3s ease, top 0.3s ease;
        pointer-events: none;
      }
    }
  }

  &__select.open {
    border: 2px solid $primary_shade;

    &::after {
      top: 55%;
      transform: rotate(135deg);
    }
  }

  &__select.open ~ .directory__select-list {
    opacity: 1;
    transform: scale(1);
  }

  &__select-list {
    position: absolute;
    top: 22px;
    z-index: 14;
    border: 1px solid $md_grey;
    border-radius: 0 0 10px 10px;
    padding: 40px 13px 32px 29px;
    width: 100%;
    background: $white;
    opacity: 0;
    transform: scale(1, 0);
    transform-origin: top;
    transition: transform 0.3s ease, opacity 0.3s ease;
  }

  &__select-item {
    &:not(:last-child) {
      margin-bottom: 11px;
    }

    .custom-checkbox__content::before {
      top: 1px;
      width: 20px;
      height: 20px;
    }

    .custom-checkbox__content::after {
      left: 6px;
      top: 3px;
      width: 8px;
      height: 13px;
    }

    .custom-checkbox__content {
      padding-left: 30px;
      font-size: 16px;
      line-height: 132%;
      color: $grey_shade;
    }
  }

  &__btn-full {
    color: $primary;
  }

  &__modul {
    overflow: hidden;
    margin-bottom: 64px;
    border-radius: 10px;
  }

  &__modul-content {
    padding: 30px;
    background-color: $black;
  }

  &__modul-subtitle {
    display: block;
    margin-bottom: -5px;
    font-family: "PlayfairDisplay", sans-serif;
    font-weight: 900;
    font-size: 24px;
    line-height: 145%;
    color: $gold;
  }

  &__modul-title {
    margin: 0;
    margin-bottom: 12px;
    max-width: 80%;
    font-weight: 400;
    font-size: 16px;
    line-height: 23px;
    color: $grey;
  }

  &__modul-link {
    display: flex;
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    color: $primary;

    svg {
      margin-left: 5px;
    }
  }

  &__catalog {
    grid-column: 9 span;

    @include mobile {
      .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
      .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
        margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 8px);
      }
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: $black;

    &--first {
      display: none;

      @include big-tablet {
        display: block;
      }
    }

    &--two {
      @include big-tablet {
        display: none;
      }
    }

    @include big-tablet {
      margin-bottom: 16px;
    }
  }

  &__swiper {
    margin-bottom: 32px;
  }

  &__pagination {
    text-align: center;

    .swiper-pagination-bullet {
      border: 2px solid $primary;
      border-radius: 10px;
      width: 32px;
      height: 32px;
      font-weight: 600;
      font-size: 16px;
      line-height: 28px;
      text-align: center;
      color: $primary;
      background: transparent;
      opacity: 1;

      @include hover {
        border-color: $primary;
        color: #fff;
        background: $primary;
      }
    }

    .swiper-pagination-bullet-active {
      border-color: $primary_shade;
      color: #fff;
      background: $primary_shade;
    }
  }
}

.directory-price {
  &__inputs {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    transition: transform 0.3s ease, opacity 0.3s ease;

    @include big-tablet {
      display: block;
      opacity: 0;
      transform: scale(1, 0);
      transform-origin: top;
    }
  }

  &__content-input {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 130%;
    color: $grey_shade;

    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  &__text {
    margin-right: 10px;

    @include big-tablet {
      display: block;
      min-width: 19px;
    }
  }

  &__label {
    display: flex;
    align-items: center;
    border: 1px solid $grey;
    border-radius: 2px;
    width: 86px;
    height: 30px;
  }

  &__input {
    outline: none;
    border: none;
    padding: 0 13px;
    width: 100%;
    font-size: 16px;
    line-height: 130%;
    color: $grey_shade;
  }

  .directory__subtitle {
    margin-bottom: 4px;
  }
}
