.choices {
  &__inner {
    border: none;
    min-height: auto;
    font-size: 16px;
    line-height: 120%;
    color: $primary;
    transition: color 0.3s ease;

    &:hover {
      color: $primary-light;
    }

    &:hover ~ .choices[data-type*="select-one"]::after {
      border-color: $primary-light;
    }

    &:hover + .choices[data-type*="select-one"]::after {
      border-color: $primary-light;
    }

    &:active {
      color: $primary-shade;

      svg {
        fill: $primary-light;
      }
    }
  }

  &__list--single {
    padding: 2px 11px 2px 5px;

    .choices__item {
      padding-right: 8px;
      width: 100%;
    }
  }

  &__placeholder {
    opacity: 1;
  }
}

.choices.focus-visible.is-focused .choices__inner {
  color: $primary;
  background-color: $pink_light;
}

.choices .choices__list--single .choices__list--dropdown .choices__item--selectable,
.choices__list[aria-expanded] .choices__item--selectable {
  padding: 0;
  padding-bottom: 1px;
}

.choices__list--dropdown .choices__item--selectable:not(:last-child),
.choices__list[aria-expanded] .choices__item--selectable:not(:last-child) {
  margin-bottom: 11px;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted,
.choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
  background-color: $pink_violet;
}

.choices__list--dropdown,
.choices__list[aria-expanded] {
  z-index: 10;
  margin-top: 2px;
  border: none;
  padding: 16px;
  width: max-content;
}

.choices .choices__list--dropdown .choices__placeholder {
  display: none;
}

.choices__item.is-selected {
  display: none;
}

.choices[data-type*="select-one"]::after {
  right: 3px;
  top: 40%;
  z-index: 3;
  border: 1px solid $primary;
  border-width: 0 0 1px 1px;
  width: 8px;
  height: 8px;
  transform: rotate(-45deg);
  transition: border-color 0.3s ease;
}

.choices[data-type*="select-one"].is-open::after {
  top: 45%;
  margin-top: 0;
  border: 1px solid $primary;
  border-width: 1px 1px 0 0;
}

.choices[data-type*="select-one"] .choices__inner {
  padding: 0;
}

.choices[data-type*="select-one"]:hover::after {
  border-color: $primary-light;
}

.choices[data-type*="select-one"]:active::after {
  border-color: $primary-shade;
}

.focus-visible.is-focused.choices[data-type*="select-one"]::after {
  border-color: $primary-shade;
}

.focus-visible.is-focused.choices .choices__inner {
  color: $primary-shade;
}
