}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  border-radius: 4px;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease, top 0.3s ease;
}
.burger::before {
  top: 0;
}
.burger::after {
  top: calc(100% - var(--burger-line-height));
}
