.search::-webkit-search-decoration,
.search::-webkit-search-cancel-button,
.search::-webkit-search-results-button,
.search::-webkit-search-results-decoration {
  display: none;
}

.search-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;

  > svg {
    position: absolute;
    left: 32px;
    fill: $grey;
    transition: fill 0.3s ease;
  }
}

.search {
  outline: none;
  border: 2px solid transparent;
  border-radius: 10px 0 0 10px;
  padding: 19px 58px;
  width: 100%;
  background-color: $grey_light;
  transition: background-color 0.3s ease, border-color 0.3s ease;

  &::-moz-placeholder {
    color: $grey;
  }

  &::-webkit-input-placeholder {
    color: $grey;
  }

  &__btn {
    position: absolute;
    right: 32px;
    line-height: 0;

    svg path,
    svg circle {
      transition: stroke 0.3s ease, fill 0.3s ease;
    }

    &:focus {
      outline: none;
    }

    &:active {
      svg path {
        stroke: $white;
      }

      svg circle {
        fill: $primary_shade;
        stroke: $primary_shade;
      }
    }

    &:disabled {
      svg path {
        stroke: $grey;
      }

      svg circle {
        stroke: $grey;
      }
    }

    @include hover {
      svg path {
        stroke: $white;
      }

      svg circle {
        fill: $primary;
      }
    }
  }

  &:focus {
    outline: none;
  }

  &:active ~ svg {
    fill: $primary;
  }

  @include hover {
    background-color: $md_grey;
  }
}

.focus-visible.search {
  border-color: $primary;
  background-color: $pink_light;
}

.focus-visible.search__btn {
  svg path {
    stroke: $white;
  }

  svg circle {
    fill: $primary_light;
    stroke: $primary;
  }
}
