html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);

  @include tablet {
    padding: 0 var(--container-offset-tablet);
    max-width: var(--container-width-tablet);
  }

  @include small-tablet {
    padding: 0 var(--container-offset-small-tablet);
    max-width: var(--container-width-small-tablet);
  }

  @include big-mobile {
    padding: 0 var(--container-offset-big-mobile);
    max-width: 100%;
  }

  @include mobile {
    padding: 0 var(--container-offset-mobile);
    max-width: 100%;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.section-title {
  margin: 0;
  margin-bottom: 26px;
  font-weight: 400;
  font-size: 32px;
  line-height: 100%;
  color: $black;

  @include small-tablet {
    width: 83%;
  }

  @include mobile {
    font-size: 24px;
  }
}

.section-offset {
  margin-bottom: 70px;
}

.btn {
  display: block;
  border: 2px solid $primary;
  border-radius: 10px;
  padding: 20px 49px;
  width: max-content;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  text-align: center;
  color: $white;
  background-color: $primary;
  transition: background-color 0.3s ease, border-color 0.3s ease, color 0.3s ease;

  &:focus {
    outline: none;
  }

  @include hover {
    border-color: $primary-light;
    background-color: $primary-light;
  }

  &:active {
    border-color: $primary-shade;
    background-color: $primary_shade;
  }

  &:disabled {
    border-color: $md_grey;
    color: $grey;
    background-color: $md_grey;
  }

  &--light {
    padding: 17px 33px;
    font-weight: 600;
    color: $primary;
    background-color: $white;

    &:focus {
      outline: none;
    }

    @include hover {
      border-color: $primary;
      color: $white;
      background-color: $primary;
    }

    &:active {
      border-color: $primary-shade;
      color: $white;
      background-color: $primary-shade;
    }
  }
}

.btn.focus-visible {
  border-color: $primary-shade;
  color: $white;
  background-color: $primary_light;
}
