.social {
  display: flex;
  align-items: center;

  &__item {
    @include mr(20px);
  }

  &__link {
    position: relative;
    display: block;
    width: 30px;
    height: 30px;
    transition: inset 0.3s ease;

    &::before {
      content: "";
      position: absolute;
      background-color: transparent;
      transition: inset 0.3s ease, background-color 0.3s ease;
      inset: 0;
    }

    svg {
      position: relative;
      z-index: 1;
      display: block;
      width: 100%;
      height: 100%;
      fill: $grey;
      object-fit: cover;
      transition: fill 0.3s ease;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      svg {
        fill: $primary;
      }
    }

    &:active {
      svg {
        fill: $primary-shade;
      }
    }
  }
}

.focus-visible.social__link {
  &::before {
    background-color: $primary-light;
    inset: -3px;
  }

  svg {
    fill: $white;
  }
}
