.suppliers {
  margin-bottom: 69px;

  &__title {
    margin-bottom: 32px;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 12px;
    font-weight: 400;
    font-size: 24px;
    line-height: 33px;
    color: $black;

    @include mobile {
      font-size: 16px;
      line-height: 22px;

      &--small {
        font-size: 14px;
        line-height: 19px;
      }
    }
  }

  &__descr {
    margin: 0;
    max-width: 842px;
    font-size: 16px;
    line-height: 182%;
    color: $grey_shade;

    @include mobile {
      font-size: 14px;
      line-height: 179%;
    }
  }

  &__offset {
    margin-bottom: 32px;
  }

  &__item {
    margin-bottom: 12px;
    padding-left: 37px;
    background-repeat: no-repeat;

    .suppliers__descr {
      max-width: 590px;
      line-height: 150%;
    }

    &--first {
      background-image: url("../img/network.svg");
    }

    &--two {
      background-image: url("../img/briefcase.svg");
    }

    &--three {
      background-image: url("../img/git-pull-request.svg");
    }
  }

  &__items {
    margin-bottom: 12px;
  }

  &__name {
    margin-bottom: 12px;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: $secondary;
  }

  &__list-ol {
    margin: 0;
    padding-inline-start: 18px;

    .suppliers__items::marker {
      font-weight: 600;
      color: $secondary;
    }

    .suppliers__descr {
      padding-left: 8px;
      max-width: 816px;
      line-height: 181%;

      @include mobile {
        line-height: 179%;
      }
    }
  }

  &__block {
    margin: 0;
    max-width: 624px;
    background-image: url("../img/pen.svg");
    background-repeat: no-repeat;

    @include mobile {
      max-width: 240px;
      background-size: 36px 64px;
    }
  }

  &__text {
    margin: 0;
    padding: 22px 0 22px 109px;
    font-size: 24px;
    line-height: 150%;
    color: $black;

    @include mobile {
      padding: 0;
      padding-left: 52px;
      font-size: 16px;
    }
  }
}
