.breadcrumps {
  margin-bottom: 32px;

  &__list {
    display: flex;
  }

  &__item {
    display: flex;
  }

  &__link {
    font-size: 14px;
    line-height: 14px;
    color: $grey;

    &:focus {
      outline: none;
    }

    @include hover {
      color: $primary;
    }

    &:active {
      color: $primary_shade;
    }

    &--active {
      color: $primary_shade;
      cursor: auto;

      @include hover {
        color: $primary_shade;
      }
    }
  }

  &__item + &__item::before {
    content: "/";
    padding-right: 3px;
    padding-left: 6px;
    font-size: 14px;
    line-height: 14px;
    color: $grey;
  }

  @include mobile {
    display: none;
  }
}

.focus-visible.breadcrumps__link {
  color: $primary_shade;
  background-color: $pink_violet;
}
