.nav {
  @include mini-tablet {
    width: 52%;
  }

  &__list {
    @include flex-v-center;

    gap: 92px;

    @include big-tablet {
      padding-bottom: 2px;
      gap: 42px;
    }

    @include mini-tablet {
      flex-flow: wrap;
      justify-content: space-between;
      gap: 0;
      column-gap: 50px;
      row-gap: 10px;
    }

    @include small-tablet {
      column-gap: 65px;
      flex-flow: wrap;
      flex-direction: column;
      align-content: start;
      align-items: flex-start;
      justify-content: start;
      margin: 0 auto;
      padding: 0 var(--container-offset-small-tablet);
      height: 100%;
      max-width: var(--container-width-small-tablet);
      row-gap: 41px;
    }

    @include mobile {
      margin-bottom: 47px;
      padding: 0 var(--container-offset-mobile);
      height: auto;
    }
  }

  &__link {
    position: relative;
    z-index: 2;
    font-size: 16px;
    line-height: 22px;
    color: $black;
    transition: color 0.3s ease;

    span {
      position: absolute;
      z-index: -1;
      transition: background-color 0.3s ease, inset 0.3s ease;
      inset: 0;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: $primary;
    }

    &:active {
      color: $primary_shade;
    }
  }

  @include small-tablet {
    position: fixed;
    left: 0;
    top: 13%;
    z-index: 20;
    padding: 92px 0 60px;
    width: 100%;
    height: 300px;
    background-image: url("../../img/burger-bg.svg");
    background-position: bottom 60px right 72px;
    background-size: 161px 112px;
    background-repeat: no-repeat;
    background-color: $white;
    visibility: hidden;
    transform: translateX(-100%);
    transition: transform 0.3s ease, visibility 0.3s ease;
  }

  @include mobile {
    top: 17%;
    padding: 62px 0 25px;
    height: 535px;
    background: none;
    background-color: $white;
  }
}

.nav.menu--active {
  visibility: visible;
  transform: translateX(0);
}

.nav__link.focus-visible {
  color: $primary_shade;

  span {
    background-color: $pink_violet;
    inset: 0 -10px;
  }
}
