.header {
  margin-bottom: 32px;

  &__container {
    background-color: $grey_light;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    padding-top: 14px;
    padding-bottom: 13px;

    @include big-tablet {
      flex-direction: column;
      padding-top: 17px;
      padding-bottom: 0;
    }

    @include small-tablet {
      margin-bottom: 32px;
    }

    @include mobile {
      margin-bottom: 27px;
      padding-top: 0;
    }
  }

  &__top-left {
    display: flex;

    @include big-tablet {
      justify-content: space-between;
      margin-bottom: 18px;
    }

    @include small-tablet {
      margin-bottom: 15px;
    }

    @include mobile {
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 27px;
    }
  }

  &__top-select {
    display: flex;
    align-items: center;
    margin-right: 30px;

    @include mobile {
      margin-right: 0;
      margin-bottom: 14px;
    }
  }

  &__label {
    font-size: 16px;
    line-height: 120%;
    color: $black;
  }

  &__phone {
    position: relative;
    display: flex;
    align-items: center;
    align-self: center;
    justify-content: center;
    font-size: 14px;
    line-height: 100%;
    color: $secondary;
    transition: inset 0.3s ease;

    &::before {
      content: "";
      position: absolute;
      background-color: transparent;
      transition: inset 0.3s ease, background-color 0.3s ease;
      inset: 0;
    }

    svg {
      z-index: 1;
      display: block;
      width: 22px;
      height: 22px;
      fill: $secondary;
      object-fit: cover;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      svg {
        fill: $secondary_light;
      }

      .header__number {
        color: $secondary_light;
      }
    }

    &:active {
      svg {
        fill: $secondary_shade;
      }

      .header__number {
        color: $secondary_shade;
      }
    }

    @include mobile {
      align-self: flex-start;
    }
  }

  &__number {
    z-index: 1;
  }

  &__submeny {
    @include flex-v-center;

    gap: 64px;

    @include mobile {
      display: none;
    }

    &--two {
      display: none;

      @include mobile {
        display: flex;
      }
    }

    @include mini-tablet {
      gap: 20px;
    }

    @include small-tablet {
      justify-content: space-between;
      margin-bottom: 30px;
      padding: 18px 0;
      gap: 0;
    }

    @include mobile {
      flex-wrap: wrap;
      padding: 0 var(--container-offset-mobile);
      row-gap: 12px;
      column-gap: 43px;
      align-items: flex-start;
    }
  }

  &__sub-item:not(:last-child) {
    @include big-mobile {
      margin-right: 10px;
      width: min-content;
    }

    @include mobile {
      margin-right: 0;
      width: auto;
      max-width: 134px;
    }
  }

  &__small-menu {
    display: flex;
    justify-content: space-between;
  }

  &__sub-link {
    position: relative;
    z-index: 2;
    font-size: 14px;
    line-height: 100%;
    color: $grey;
    transition: color 0.3s ease;

    span {
      position: absolute;
      z-index: -1;
      transition: background-color 0.3s ease, inset 0.3s ease;
      inset: 0;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: $primary;
    }

    &:active {
      color: $primary_shade;
    }
  }

  &__top-right {
    display: flex;
    justify-content: space-between;

    @include big-tablet {
      padding: 11px 0;
    }

    @include small-tablet {
      flex-direction: column;
      padding: 0;
    }
  }

  &__middle {
    display: flex;
    align-items: center;
    padding: 32px 0;

    @include small-tablet {
      padding: 0;
    }
  }

  &__link {
    margin-right: auto;
    outline: 2px solid transparent;
    outline-offset: 0;
    border-radius: 10px;
    transition: outline 0.3s ease, outline-offset 0.3s ease;

    &:focus {
      outline: none;
    }

    @include small-tablet {
      display: none;
      margin-right: 0;
    }

    /* stylelint-disable-next-line order/order */
    &--two {
      display: none;

      @include small-tablet {
        display: block;
      }

      @include mobile {
        display: none;
      }
    }

    &--three {
      display: none;

      @include mobile {
        display: block;
        align-self: center;
        margin-bottom: 18px;
        padding-top: 12px;
        padding-bottom: 11px;
      }
    }
  }

  &__bottom {
    display: flex;
  }

  &__search {
    display: flex;
    align-items: center;
    margin-right: 120px;
    width: 100%;

    .choices {
      width: 300px;

      &__inner {
        border: 2px solid $grey_light;
        border-radius: 0 10px 10px 0;
        transition: border-color 0.3s ease, color 0.3s ease;

        &:active {
          border-color: $primary-shade;
        }

        @include hover {
          border-color: $primary;
        }
      }

      &__list--single {
        padding: 18.5px 30px;
      }

      @include small-tablet {
        width: 189px;
      }

      @include mobile {
        width: 100%;
      }
    }

    .choices[data-type*="select-one"]::after {
      right: 34px;
      top: 47%;
    }

    @include tablet {
      margin-right: 0;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__select {
    .choices.focus-visible.is-focused .choices__inner {
      border-color: $primary;
      background-color: $pink_light;
    }

    .choices__list--dropdown,
    .choices__list[aria-expanded] {
      margin-top: 0;
      border-radius: 0 0 10px  10px;
      padding: 22px 30px;
      width: 100%;
      max-height: 194px;
    }

    .choices__list {
      display: flex;
      flex-flow: wrap;
      flex-direction: column;
    }

    .choices__item {
      width: max-content;
    }

    .choices__list--dropdown .choices__list,
    .choices__list[aria-expanded] .choices__list {
      align-content: flex-start;
      max-height: 140px;
    }

    .choices__list--dropdown .choices__item--selectable:not(:last-child),
    .choices__list[aria-expanded] .choices__item--selectable:not(:last-child) {
      margin-bottom: 0;
    }

    .choices__list--dropdown .choices__item--selectable:not(:nth-child(6n)),
    .choices__list[aria-expanded] .choices__item--selectable:not(:nth-child(6n)) {
      margin-bottom: 11px;
    }

    .choices__list--dropdown .choices__item--selectable:not(:nth-child(n+6)),
    .choices__list[aria-expanded] .choices__item--selectable:not(:nth-child(n+6)) {
      margin-right: 32px;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__lk {
    display: flex;
    align-items: center;

    &--top {
      display: none;
    }

    @include tablet {
      &--top {
        display: flex;
      }

      &--bottom {
        display: none;
      }
    }
  }

  &__user {
    position: relative;
    margin-right: 32px;
    line-height: 0;

    &::before {
      content: "";
      position: absolute;
      background-color: transparent;
      transition: inset 0.3s ease, background-color 0.3s ease;
      inset: 0;
    }

    svg {
      position: relative;
      z-index: 1;
      width: 26px;
      height: 28px;
      fill: $primary;
      transition: fill 0.3s ease;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      svg {
        fill: $primary_light;
      }
    }

    &:active {
      svg {
        fill: $primary_shade;
      }
    }
  }

  &__shopping-cart {
    position: relative;
    line-height: 0;

    &::before {
      content: "";
      position: absolute;
      background-color: transparent;
      transition: inset 0.3s ease, background-color 0.3s ease;
      inset: 0;
    }

    svg {
      position: relative;
      z-index: 1;

      .path-2 {
        transition: fill 0.3s ease;
      }
    }

    &:focus {
      outline: none;
    }

    @include hover {
      svg .path-2 {
        fill: $primary_light;
      }
    }

    &:active {
      svg .path-2 {
        fill: $primary_shade;
      }
    }
  }

  @include mobile {
    margin-bottom: 33px;
  }
}

.focus-visible.header__user {
  &::before {
    background-color: $primary-light;
    inset: -4px;
  }

  svg {
    fill: $white;
  }
}

.focus-visible.header__shopping-cart {
  &::before {
    background-color: $primary-light;
    inset: -4px;
  }

  svg .path-2 {
    fill: $white;
  }
}

.header__sub-link.focus-visible {
  color: $primary_shade;

  span {
    background-color: $pink_violet;
    inset: -1px -10px;
  }
}

.focus-visible.header__phone {
  &::before {
    background-color: $bone;
    inset: -1px;
  }

  svg {
    fill: $secondary;
  }

  .header__number {
    color: $secondary;
  }
}

.focus-visible.header__link {
  outline: 2px solid $primary;
  outline-offset: 2px;
}

.search-wrapper {
  @include mobile {
    margin-bottom: 17px;
  }
}
