.tooltip {
  &__btn {
    position: relative;
    vertical-align: middle;
    line-height: 0;

    .tooltip__svg--1 {
      position: absolute;
      opacity: 1;
      visibility: visible;
      transition: opacity 0.3s ease, visibility 0.3s ease;
    }

    .tooltip__svg--2 {
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.3s ease, visibility 0.3s ease;
    }

    @include hover {
      .tooltip__svg--1 {
        opacity: 0;
        visibility: hidden;
      }

      .tooltip__svg--2 {
        opacity: 1;
        visibility: visible;
      }

      +.tooltip__txt {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__txt {
    position: relative;
    padding: 5px;
    max-width: 157px;
    font-size: 9px;
    line-height: 14px;
    text-align: center;
    color: $white;
    background-color: $black;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease, visibility 0.3s ease;

    &::before {
      content: "";
      position: absolute;
      right: 50%;
      bottom: 0;
      width: 12px;
      height: 12px;
      background-color: inherit;
      transform: rotate(45deg) translateX(50%);
    }
  }
}
