.top {
  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 32px;

    @include big-tablet {
      grid-template-columns: repeat(9, 1fr);
    }

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
    }

    @include big-mobile {
      column-gap: 16px;
      row-gap: 32px;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 30px 30px 29px;
    background-color: $grey_light;

    &:nth-child(n+1) {
      grid-column: 6 span;
    }

    &:nth-child(n+3) {
      grid-column: 4 span;
    }

    @include big-tablet {
      &:first-child {
        grid-column: 6 span;
      }

      &:nth-child(n+2) {
        grid-column: 3 span;

        .top__subtitles {
          margin-bottom: 36px;
        }

        picture {
          margin-bottom: 37px;
        }
      }

      &:nth-child(3) {
        grid-column: 7 / 3 span;
        grid-row: 1/2;
      }

      &:nth-child(4) {
        grid-column: 3 span;
        grid-row: 2/3;
      }
    }

    @include small-tablet {
      &:nth-child(n+2) {
        grid-column: 3 span;
        grid-row: auto;
      }

      &:nth-child(3) {
        grid-column: 3 span;
        grid-row: 2 / 3;
      }
    }

    @include big-mobile {
      &:nth-child(n+2) {
        .top__subcategories-list {
          flex-direction: column;
          gap: 10px;
        }
      }
    }

    @include mobile {
      padding-bottom: 16px;

      &:nth-child(n+2) {
        padding: 16px;

        .top__links {
          font-size: 10px;
        }

        .top__subtitles {
          margin-bottom: 15px;
          font-size: 16px;
          line-height: 22px;
        }

        .top__subcategories-list {
          margin-bottom: 8px;
          gap: 4px;
        }

        picture {
          margin-right: -16px;
          margin-bottom: 0;
          margin-left: -16px;
        }
      }

      &:nth-child(4) {
        .top__subtitles {
          line-height: 100%;
        }
      }
    }
  }

  &__subcategories-list {
    display: flex;
    margin-bottom: 14px;
    gap: 30px;

    @include mobile {
      gap: 20px;
    }
  }

  &__links {
    position: relative;
    z-index: 2;
    display: block;
    font-size: 14px;
    line-height: 100%;
    color: $grey;
    transition: color 0.3s ease;

    span {
      position: absolute;
      z-index: -1;
      transition: background-color 0.3s ease, inset 0.3s ease;
      inset: 0;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: $primary;
    }

    &:active {
      color: $primary_shade;
    }
  }

  &__subtitles {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 24px;
    line-height: 33px;
    color: $black;

    @include mobile {
      margin-bottom: 13px;
    }
  }

  picture {
    display: block;
    margin-bottom: 31px;
    margin-left: -30px;

    @include big-tablet {
      margin-right: -30px;
    }

    @include mobile {
      margin-bottom: 0;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    margin-top: auto;
    width: max-content;
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    text-decoration: underline transparent 2px;
    color: $primary;
    transition: text-decoration-color 0.3s ease, color 0.3s ease;

    svg {
      margin-left: 6px;
    }

    svg path,
    svg circle {
      transition: stroke 0.3s ease, fill 0.3s ease;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      text-decoration-color: $primary;

      svg path {
        stroke: $white;
      }

      svg circle {
        fill: $primary;
      }
    }

    &:active {
      text-decoration-color: $primary_shade;
      color: $primary_shade;

      svg path {
        stroke: $white;
      }

      svg circle {
        fill: $primary_shade;
        stroke: $primary_shade;
      }
    }

    @include mobile {
      display: none;
    }

    &--short {
      display: none;

      @include mobile {
        display: inline-block;
        vertical-align: middle;
        line-height: 0;

        svg {
          margin-left: 0;
        }
      }
    }
  }

  @include mobile {
    margin-bottom: 78px;
  }
}

.focus-visible.top__link {
  text-decoration-color: $primary;

  svg path {
    stroke: $white;
  }

  svg circle {
    fill: $primary_light;
    stroke: $primary;
  }
}

.focus-visible.top__links {
  color: $primary_shade;

  span {
    background-color: $pink_violet;
    inset: -1px -10px;
  }
}
