.footer {
  &__top {
    padding: 24px 0;
    background-color: $md-grey;
  }

  &__content {
    display: flex;
    justify-content: space-between;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__left {
    @include small-tablet {
      display: flex;
      justify-content: space-between;
      margin-bottom: 25px;
    }

    @include mobile {
      flex-direction: column;
      align-items: center;
    }
  }

  &__link {
    display: inline-block;
    margin-bottom: 28px;
    outline: 2px solid transparent;
    outline-offset: 0;
    border-radius: 10px;
    transition: outline 0.3s ease, outline-offset 0.3s ease;

    &:focus {
      outline: none;
    }

    @include small-tablet {
      margin-bottom: 0;
    }

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__right {
    width: 65.8%;

    @include big-tablet {
      width: 77%;
    }

    @include small-tablet {
      width: 100%;
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
    padding-top: 2px;

    @include mobile {
      display: grid;
      column-gap: 16px;
      grid-template-columns: repeat(6, 1fr);
      row-gap: 26px;
    }
  }

  &__item {
    grid-column: 3 span;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 5px;
    font-weight: 400;
    font-size: 16px;
    line-height: 100%;
    color: $black;
  }

  &__subitem {
    @include mb(5px);
  }

  &__sublink {
    position: relative;
    z-index: 2;
    font-size: 14px;
    line-height: 100%;
    color: $grey;
    transition: color 0.3s ease;

    span {
      position: absolute;
      z-index: -1;
      transition: background-color 0.3s ease, inset 0.3s ease;
      inset: 0;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: $primary;
    }

    &:active {
      color: $primary_shade;
    }
  }

  &__box {
    text-align: center;
  }

  &__copyrite {
    display: inline-block;
    padding: 5px 0;
    font-size: 14px;
    line-height: 19px;
    color: $grey;
  }
}

.focus-visible.footer__sublink {
  color: $primary_shade;

  span {
    background-color: $pink_violet;
    inset: -1px -10px;
  }
}

.focus-visible.footer__link {
  outline: 2px solid $primary;
  outline-offset: 2px;
}
