.offers {
  &__slide {
    width: calc(((100% - (32px * 2)) / 4) - 8px);

    &:nth-child(3n+2) {
      width: calc(((100% - (32px * 2)) / 2) + 16px);
    }

    @include big-tablet {
      width: auto;

      &:nth-child(3n+2) {
        width: auto;

        .offers__item {
          picture {
            margin-bottom: 48px;
          }
        }
      }
    }
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column-reverse;
    padding: 30px;
    min-height: 511px;
  }

  &__content {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    background-position: right bottom;
    background-repeat: no-repeat;

    &--bg1 {
      background-image: url("../../img/scheme_1.jpg");
      background-size: 230px 107px;
    }

    &--bg2 {
      background-image: url("../../img/scheme_2.jpg");
      background-size: 234px 124px;
    }

    @include big-tablet {
      background: none;
    }
  }

  &__left-content {
    display: flex;
    flex-direction: column-reverse;
  }

  picture {
    display: flex;
    justify-content: center;
    margin-bottom: 16px;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
    max-width: 225px;
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: $black;
  }

  &__price {
    display: flex;
    margin-bottom: 23px;
    gap: 30px;

    &--sell {
      font-size: 18px;
      line-height: 130%;
      color: $secondary;
    }

    &--orig {
      display: flex;
      align-items: flex-end;
      font-size: 16px;
      line-height: 130%;
      text-decoration-line: line-through;
      color: $grey;
    }
  }

  &__ribbon {
    position: absolute;
    left: 0;
    top: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-clip-path: polygon(0% 0%, 89% 0%, 100% 50%, 89% 100%, 0% 100%);
    clip-path: polygon(0% 0%, 89% 0%, 100% 50%, 89% 100%, 0% 100%);
    padding-right: 8px;
    width: 92px;
    height: 34px;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    color: $white;
    background: $secondary;
  }
}

.swiper-button-next,
.swiper-button-prev {
  top: 4%;
  width: max-content;
  height: auto;

  @include mobile {
    top: auto;
    bottom: 0;
  }
}

.swiper-button-prev,
.swiper-rtl .swiper-button-next {
  left: auto;
  right: 45px;

  @include mobile {
    right: 53%;
  }
}

.swiper-button-next,
.swiper-rtl .swiper-button-prev {
  right: 0;

  @include mobile {
    right: 37%;
  }
}

.swiper-button-next::after,
.swiper-rtl .swiper-button-prev::after {
  content: "";
}

.swiper-button-prev::after,
.swiper-rtl .swiper-button-next::after {
  content: "";
}

.swiper-button-next.swiper-button-disabled,
.swiper-button-prev.swiper-button-disabled {
  opacity: 1;
}

.swiper-button-next.swiper-button-disabled svg path,
.swiper-button-next.swiper-button-disabled svg circle,
.swiper-button-prev.swiper-button-disabled svg path,
.swiper-button-prev.swiper-button-disabled svg circle {
  stroke: $grey;
}

.swiper-offers {
  @include mobile {
    padding-bottom: 48px;
  }
}
