.feedback {
  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 32px;

    @include big-tablet {
      grid-template-columns: repeat(9, 1fr);
    }

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
    }
  }

  &__content {
    grid-column: 6 span;

    @include big-tablet {
      grid-column: 4 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__title {
    margin-bottom: 10px;

    @include big-tablet {
      width: 100%;
    }

    @include mobile {
      margin-bottom: 6px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 15px;
    font-size: 14px;
    line-height: 19px;
    color: $grey_shade;

    @include big-tablet {
      margin-bottom: 16px;
    }
  }

  &__image {
    grid-column: 6 span;
    margin: auto 0;
    width: 624px;
    height: 383px;
    background: url("../img/feedback_bg.webp");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    @include big-tablet {
      grid-column: 5 span;
      width: 514px;
      height: 316px;
    }

    @include small-tablet {
      grid-column: 2 span;
      width: 187px;
      height: 444px;
      background: url("../../img/feedback_bg-2.webp");
    }

    @include mobile {
      display: none;
    }
  }
}
